import React from "react";
import { Link } from "react-router-dom";
import { images } from "../../constants";
const LazySeasonCategory = ({ category }) => {
  return (
    <>
      {category.map((categoryItem, index) => (
        <div className="row category" key={index}>
          <div className="col-md-3 col-sm-12">
          {/* <LazyLoad key={index}> */}
            <div className="image-container">
              <img
                src={`data:image/png;base64,${categoryItem.category_Image}`}
                alt={images.ImageNotAvailable}
                className="shadow-lg catalog"
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src = images.ImageNotAvailable;
                }}
              />
            </div> 
            {/* </LazyLoad> */}
          </div>
         
          <div className="col-md-9 p-0  col-sm-12">
            <div className="row">
              <div className="col-12">
                <div className="">
                  <p className="header">{categoryItem.category_Name}</p>
                </div>
              </div>
              <div className="col-12">
                <div className="">
                  <Link
                    className="text-black catalog-link"
                    to={`/shoppingCart/${categoryItem.category_id}`}
                  >
                    <div className="row Items__Category">
                      240 Items Available
                    </div>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}
    </>
  );
};

export default LazySeasonCategory;
